@import url(https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&family=New+Tegomin&display=swap);
body,
html {
  width: 100vw;
  height: 100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  height: 100%;
  background: #f3f0f1;
  font-family: "New Tegomin", serif;
  /* background: linear-gradient(97.93deg, #4452ce81 43.88%, #6644cc83 109.61%); */
}

.App header {
  padding: 18px 25px;
  font-family: "New Tegomin", serif;
  font-size: 1.9em;
  /* box-shadow: 0 10px 35px rgba(0, 0, 0, 0.1);
  border-radius: 7px; */
}

.App header span {
  color: #f39489;
}

.App header a {
  text-decoration: none;
  color: #080808;
}

.App header .button {
  font-size: 0.7em;
  text-decoration: none;
  float: right;
  background: #f3f0f1;
  position: relative;
  font-family: "Nanum Brush Script", cursive;
  padding: 10px 15px;
  margin-right: 20px;
  border-radius: 32px;
  border: none;
  text-align: center;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
  box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
    6px 6px 10px rgba(0, 0, 0, 0.2);
  color: #f39489;
}

.App header .button:hover {
  opacity: 0.7;
  box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
    6px 6px 10px rgba(0, 0, 0, 0.2);
  color: #df887e;
}
.App header .button:active {
  opacity: 1;
  box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
    inset 8px 8px 16px rgba(0, 0, 0, 0.1);
  color: rgba(1, 121, 1, 0.533);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App header .button img {
  margin: 0 5px;
}

.App footer {
  background-color: #f39489;
  padding: 10px;
  color: #fff;
  text-align: right;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (max-width: 599px) {
  .App header .button {
    padding: 5px 8px;
    margin-right: 10px;
  }

  .App .linkText {
    display: none;
  }

  .App footer {
    font-size: small;
  }
}

